<template>
	<div>
		<!-- 主体表单 -->
		<div class="main1">
      <div class="logo">
        <img src="../../assets/img/logo.png">
      </div>
			<div class="title_des">{{ app_title }}</div>
        <mu-container>
          <mu-text-field v-model="phoneData" type="text" placeholder="电话号码" icon="account_circle"></mu-text-field><br/>
          <mu-text-field v-model="passData" type="password" icon="locked"  placeholder="输入信息"></mu-text-field><br/>
        </mu-container>
		</div>
    <mu-button @click="startLogin" color="primary">登录</mu-button>
		<!-- 底部信息 -->
<!--		<div class="footer" style="margin-top: 16px">-->
<!--			<div>-->
<!--        <router-link to="/register_select">新用户注册</router-link>-->
<!--			</div>-->
<!--			<div style="margin-left: 32%;">-->
<!--        <router-link to="/register_select">忘记密码</router-link>-->
<!--			</div>-->
<!--		</div>-->

		<div class="footer_des">你的省钱专家</div>
	</div>
</template>

<script>
	var _this;


	export default {
		data() {
			return {
			  app_title:"管理后台",
				//logo图片 base64
				logoImage: 'data:image/png;base64,',
				phoneData: '', //用户/电话
				passData: '', //密码
				isRotate: false //是否加载旋转
			};
		},

		mounted() {
			_this = this;
		},
		destroyed() {
		},
		methods: {
			startLogin() {

        if (this.phoneData.length == 0) {
          this.$toast.error('请输入账号');
        	return;
        }
        if (this.passData.length == 0) {
          this.$toast.error('请输入密码');
        	return;
        }

        let data ={
          username:this.phoneData,
          password:this.passData
        }


        this.$store.dispatch('sysUser/login',data)
          .then(() => {
            this.$router.push({ path: this.redirect || '/home', query: this.otherQuery });
            this.$toast.message('登录成功');
          })
          .catch((e) => {
            this.$toast.error('账号或密码不正确');
          })

			},
		}
	};
</script>

<style>
	@import url('../../assets/css/main.css');
	@import url('../../assets/css/icon.css');
	.type {
		display: flex;
		margin-left: 16px;
		border-bottom: 1px solid #eeeeee;
		width: 100%;
	}

  .logo {
    font-weight: bold;
    color: #0055b8;
    font-size: 22px;
    text-align: center;
    margin-bottom: 16px;
    margin-top: 16px;
  }

	.title_des {
		font-weight: bold;
		color: #0055b8;
		font-size: 22px;
    text-align: center;
		margin-bottom: 16px;
	}

	.login_icon_wx {
		color: #999999;
		border: none;
	}

	.weixinLogin {
		color: #999999;
		text-align: center;
		font-size: 12px;
		margin-top: 20px;
		left: auto;
		right: auto;
	}

	.footer_des {
		color: #666666;
		text-align: center;
		font-size: 14px;
		margin-top: 40px;
	}

	.getCode {
		font-size: 14px;
		margin-left: 40px;
		color: #0055b8;
	}

	.item {
		line-height: 40px;
		display: flex;
	}

	.login_image {
		margin-top: 8px;
		width: 20px;
		height: 25px;
	}
  .main1 {
    margin: 0px 16px
  }

	.main-input {
		font-size: 14px;
		margin-left: 16px;
		border-bottom: 1px solid #eeeeee;
		height: 40px;
		line-height: 40px;
	}

	.main-input-code {
		width: 70%;
		font-size: 14px;
		height: 40px;
		line-height: 40px;
	}

	.btn_login {
		color: #ffffff;
		font-size: 16px;
		width: 260px;
		height: 40px;
		background: #0055b8;
		border-radius: 8px;
		line-height: 40px;
		text-align: center;
		margin-left: auto;
		margin-right: auto;
		margin-top: 45px;
	}

	.main-input {
		flex: 1;
		text-align: left;
		font-size: 28 upx;
		padding-right: 10 upx;
		margin-left: 20 upx;
	}
</style>



